import React, {useState} from 'react'
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import logo from "../img/SAFEWAY-LOGO.png"
import "./Navbar.css"
import { Link } from 'react-router-dom';




const Navbar1 = () => {
  const [expanded, setExpanded] = useState(false);

  const handleToggle = () => setExpanded(!expanded);

  const closeMenu = () => setExpanded(false);
  return (
    <Navbar expand="lg" className="" expanded={expanded}>
    <Container className='justify-content-between'>
      <Link to="Home"><img src={logo} alt="" className="logo" /></Link>
       <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={handleToggle} />
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="me-auto">
          <Link  to="About" onClick={closeMenu}>About Us</Link>
          <Link to="Destination" onClick={closeMenu}>Destinations </Link>
          <Link to="CarRental" onClick={closeMenu}>Car Rental</Link>
          <Link to="FlightTickets" onClick={closeMenu}>Flight Tickets</Link>
          <Link to="Blog" onClick={closeMenu}>Blogs</Link>
          <Link to="Gallery" onClick={closeMenu}>Gallery</Link>
          <Link to="Contact" onClick={closeMenu}>Contact Us</Link>
          
        </Nav>
      </Navbar.Collapse>
    </Container>
  </Navbar>
  )
}

export default Navbar1