import React from 'react'
import "./WhyChoose.css"
import Choose from './Choose'
const Whychoose = () => {
  return (
    <section className="pt-5 pb-5 ">
      <div className="container">
        
        <div className="row">
<div className="col-md-12">
<div className="w-md-80 w-lg-50 text-center mx-md-auto mb-5 mt-4">
                    <h2 className="section-title text-black font-size-30 font-weight-bold mb-0">
                    Why Choose Safeway Go
                    </h2>
                </div>
</div>
          {
            Choose.map((elm, id)=>{
              return(
                <div className="col-md-6 col-lg-4 col-xl-3 mb-3 mb-md-4 pb-1">
               <div className="support-content">
                            <img src={elm.imdrs} alt="" />
                            <h3>{elm.titele}</h3>
                            <p>{elm.dis}</p>
                        </div>
            </div>
          )
            })
          }
        </div>
      </div>
   </section>
  )
}

export default Whychoose