import React from 'react'
import BlogData from './BlogData';
import { useParams } from 'react-router-dom';
const BlogDetail = () => {
    const { index } = useParams();
  const BlogDataN = BlogData.find(BlogData => BlogData.index === parseInt(index));

  if (!BlogData) {
    return <h2>Blog not found</h2>;
  }
  return (
<>
<div className="hero-block hero-v1 bg-img-hero-bottom gradient-overlay-half-black-gradient text-center z-index-2" >
   <div className="container space-3 space-top-xl-5">
      <div className="row pb-xl-8">
         <div className="col-md-12">
            <h1 className="font-size-40 font-size-xs-30  font-weight-bold text-uppercase text-left" style={{color: "#f58220"}} >Blog </h1>
         </div>
         <div className="col-md-12">
            <nav className="py-3" aria-label="breadcrumb">
               <ol className="breadcrumb breadcrumb-no-gutter mb-0 flex-nowrap flex-xl-wrap overflow-auto overflow-xl-visble">
                  <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="/">Home</a></li>
                  <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="#">Blog Detail</a></li>
                  <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="#">{BlogDataN.title}</a></li>
               </ol>
            </nav>
         </div>
      </div>
   </div>
</div>

    <section className="pt-5 pb-5 ">
      <div className="container">
        
        <div className="row justify-containt-center">
  
          
                <div className="col-md-8">

                <div  className="blog-post">
                    <div className="img1">
            <img src={BlogDataN.image} alt={BlogDataN.title} />
            </div>
            <div className="conta">
           <h3>{BlogDataN.title}</h3>

         <p>{BlogDataN.logD}</p>
          </div>
          </div>
          </div>
           
        </div>
      </div>
   </section>
   </>
  )
}

export default BlogDetail