import React from 'react'
import "./Trending.css";
import {TrandinData} from './TrandinData';
const Trending = () => {
  return (
    <section className="pt-5 pb-5 ">
      <div className="container">
        
        <div className="row">
<div className="col-md-12">
<div className="w-md-80 w-lg-50 text-center mx-md-auto mb-5 mt-4">
                    <h2 className="section-title text-black font-size-30 font-weight-bold mb-0">
                    Hottest Trending Getaways

                    </h2>
                </div>
</div>
          {
            TrandinData.map((elm, id)=>{
              return(
                <div className="col-md-6 col-lg-4 col-xl-3 mb-3 mb-md-4 pb-1">
                <div className="card card1 mb-1 transition-3d-hover shadow-hover-2 tab-card h-100">
                    <div className="position-relative mb-2">
                        <a href={elm.link} className="d-block gradient-overlay-half-bg-gradient-v5">
                            <img src={elm.imdrs1} alt='' />

                        </a>
                        <div className="position-absolute bottom-0 left-0 right-0">
                            <div className="px-3 pb-2">
                            </div>
                        </div>
                    </div>
                    <div className="card-body px-4 py-2">
                        <a href={elm.link} className="d-block">
                            <div className="mb-1 d-flex align-items-center font-size-14 text-gray-1">
                               <i class="fa fa-map-marker" aria-hidden="true"></i> {elm.titele}
                                </div>
                        </a>
                        <a href={elm.link} className="card-title font-size-17 font-weight-bold mb-0 text-dark">{elm.sutitel}</a>
                        <a className="home-read-more-two" href={elm.link}>View More</a>
                    </div>
                </div>
            </div>
          )
            })
          }
        </div>
      </div>
   </section>
  )
}

export default Trending