import React from 'react'
import "./Destrination.css"
import InnsedestinatioData from './InnsedestinatioData';

import {Link } from 'react-router-dom';

const Destination = () => {
  return (
   <>
    <div className="hero-block hero-v1 herrobg bg-img-hero-bottom gradient-overlay-half-black-gradient text-center z-index-2"  >
   <div className="container space-3 space-top-xl-5">
      <div className="row pb-xl-8">
         <div className="col-md-12">
            <h1 className="font-size-40 font-size-xs-30  font-weight-bold text-uppercase text-left" style={{color: "#f58220"}} >DESTINATIONS
</h1>
         </div>
         <div className="col-md-12">
            <nav className="py-3" aria-label="breadcrumb">
               <ol className="breadcrumb breadcrumb-no-gutter mb-0 flex-nowrap flex-xl-wrap overflow-auto overflow-xl-visble">
                  <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="index.html">Home</a></li>
                  <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="#">DESTINATIONS
</a></li>
               </ol>
            </nav>
         </div>
      </div>
   </div>
</div>
<section className="pt-5 pb-5 ">
      <div className="container">
        
        <div className="row">
<div className="col-md-12">
<div class="w-md-80 w-lg-50 text-center mx-md-auto mb-5 mt-4">
                    <h2 class="section-title text-black font-size-30 font-weight-bold mb-0">
                        Discover Your Destinations
                    </h2>
                </div>
</div>
          {
            InnsedestinatioData.map((elm, id)=>{
              return(
                <div className="col-md-3" key={id}>
              <div key={id} className="min-height-350 mb-4 bg-img-hero rounded-border p-5 gradient-overlay-half-bg-gradient transition-3d-hover shadow-hover-2 border-0 dropdown" style={{backgroundImage: `url(${elm.imdrs})`,backgroundRepeat:'no-repeat',backgroundSize: "cover"}}>
              <div className="w-100 d-flex justify-content-between mb-2">
                  <div className="position-relative">

                      <a href={elm.link} className="destination text-white font-weight-bold font-size-21 pb-3 mb-1 text-lh-1 d-block">{elm.titele}</a>
                      
                      <Link to={`/InnsedestinatioData/${elm.id}`} className='home-read-more'>Read More</Link>
                      </div>
                  </div>
              </div>
          </div>)
            })
          }
        </div>
      </div>
   </section>
 </>
  )
}

export default Destination