import React from 'react'
import "./Fleets.css"
import Slider from 'react-slick';
import LogosMin from './LogosMin';
const Fleets = () => {
const settings = {
dots: false,
infinite: true,
speed: 500,
slidesToShow: 5,
slidesToScroll: 1,
autoplay: true,
autoplaySpeed: 2000,
responsive: [
{
breakpoint: 1024,
settings: {
slidesToShow: 4,
slidesToScroll: 1,
infinite: true,
dots: true
}
},
{
breakpoint: 600,
settings: {
slidesToShow: 2,
slidesToScroll: 1,
initialSlide: 2
}
},
{
breakpoint: 480,
settings: {
slidesToShow: 2,
slidesToScroll: 1,
initialSlide: 2
}
}
]
};
return (
<section className="pt-5 pb-5 " style={{overflowX:'hidden'}}>
   <div className="container">
      <div className="row">
         <div className="col-md-12">
            <div className="w-md-80 w-lg-50 text-center mx-md-auto mb-5 mt-4">
               <h2 className="section-title text-black font-size-30 font-weight-bold mb-0">
                  Our Fleets
               </h2>
            </div>
         </div>
         <div className="col-md-12">
            <div className="client-logo-carousel">
               <Slider {...settings}>
                  {LogosMin.map((emlm, index) => (
                  <div key={index}>
                     <img src={emlm.imgrec} alt={`Client logo ${index + 1}`} />
                  </div>
                  ))}
               </Slider>
            </div>
         </div>
      </div>
   </div>
</section>
)
}
export default Fleets