import React, { useState, useEffect } from 'react';
import { Form, Button, Row,  Tab, Nav } from 'react-bootstrap';
import CityData from '../banner1/CityData';
import CityDataTo from '../banner1/CityDataTo';
const Tab1 = () => {
  const [selectedState, setSelectedState] = useState('');
  const [selectedState1, setSelectedState1] = useState('');
 

  useEffect(() => {
    setSelectedState()
  }, [selectedState]);

  useEffect(() => {
    setSelectedState1()
  }, [selectedState1]);
  return (
    <div className="tabn main">
    <Tab.Container defaultActiveKey="flight">
      <Nav variant="tabs">
        <Nav.Item>
          <Nav.Link eventKey="flight">   One Way</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="hotel">   Round Trip</Nav.Link>
        </Nav.Item>
       
      </Nav>
      <Tab.Content className="mt-4">
        <Tab.Pane eventKey="flight"> 
          <Form action='https://formspree.io/f/xoqgoyqz' method='post'>
            <Row className="mb-3 justify-containt-center">
              <div className='col-md-3 cont' >
                <Form.Group controlId="formFrom">
                <Form.Label>From</Form.Label>
                <div className='d-flex formm'>
                 
                
                <select value={selectedState} className="form-control" name='outstationfrom' onChange={e => setSelectedState(e.target.value)}>
          <option value="">Select Place</option>
          {CityData.map(state => (
            <option key={state.id} value={state.id}>{state.name}</option>
          ))}
        </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>

                  </div>
                </Form.Group>
              </div>
              <div className='col-md-3 cont' >
                <Form.Group controlId="formFrom">
                <Form.Label>To <span style={{visibility:'hidden'}}>om</span></Form.Label>
                <div className='d-flex formm'>
                 
                
                <select value={selectedState1} className="form-control" name='Outstationto' onChange={e => setSelectedState1(e.target.value)}>
          <option value="">Select Place</option>
          {CityDataTo.map(state => (
            <option key={state.id} value={state.id}>{state.name}</option>
          ))}
        </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>

                  </div>
                </Form.Group>
              </div>
              <div className='col-md-2 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label> Date</Form.Label>
                  <Form.Control type="date" name='Outstationdate' placeholder='' />
                </Form.Group>
              </div>
              <div className='col-md-2 cont'  >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>ADULTS (18+)</Form.Label>
                  <div className='d-flex formm'>
                  <select className="form-control" name='Outstationclass'>
                  <option>1</option>
                          <option>2</option>
                          <option>3</option>
                  </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>
                  </div>
                </Form.Group>
              </div>
              <div className='col-md-2 cont' >
              <Form.Group controlId="formDepartureDate" >
                  <Form.Label>CHILDREN (0-17)</Form.Label>
                  <div className='d-flex formm'>
                  <select className="form-control" name='OutstationChildrenClass'>
                          <option>Children (0-17)</option>
                          <option>1</option>
                          <option>2</option>
                          <option>3</option>
                      </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>
                      </div>
                </Form.Group>
              </div>
            </Row>
            <Row className="mb-3 justify-containt-center">
            <div className='col-md-4 cont'  >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>Name</Form.Label>
                  <Form.Control type="text" placeholder=' Name'  name='Outstationuser'/>
                </Form.Group>
              </div>
              <div className='col-md-4 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>PHONE</Form.Label>
                  <Form.Control type="text" placeholder=' Phone Number' name="Outstationphone" />
                </Form.Group>
              </div>
              <div className='col-md-4 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>E-MAIL</Form.Label>
                  <Form.Control type="text" placeholder=' E-mail' name='Outstationemail' />
                </Form.Group>
              </div>
              <div className='col-md-5 ' >
            <div className='btnalign'>
              <Button variant="primary" type="submit">
            Submit
            </Button>
            </div>
              </div>
            </Row>
           
          </Form>
        </Tab.Pane>
        <Tab.Pane eventKey="hotel">
        <Form action='https://formspree.io/f/xoqgoyqz' method='post'>
            <Row className="mb-3 justify-containt-center">
              <div className='col-md-3 cont' >
                <Form.Group controlId="formFrom">
                <Form.Label>From</Form.Label>
                <div className='d-flex formm'>
                 
                
                <select value={selectedState} className="form-control" name='Localfrom' onChange={e => setSelectedState(e.target.value)}>
          <option value="">Select Place</option>
          {CityData.map(state => (
            <option key={state.id} value={state.id}>{state.name}</option>
          ))}
        </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>

                  </div>
                </Form.Group>
              </div>
              <div className='col-md-3 cont' >
                <Form.Group controlId="formFrom">
                <Form.Label>To <span style={{visibility:'hidden'}}>om</span></Form.Label>
                <div className='d-flex formm'>
                 
                
                <select value={selectedState1} className="form-control" name='Localto' onChange={e => setSelectedState1(e.target.value)}>
          <option value="">Select Place</option>
          {CityDataTo.map(state => (
            <option key={state.id} value={state.id}>{state.name}</option>
          ))}
        </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>

                  </div>
                </Form.Group>
              </div>
              <div className='col-md-2 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label> Date</Form.Label>
                  <Form.Control type="date" name='Localdate' placeholder='' />
                </Form.Group>
              </div>
              <div className='col-md-2 cont'  >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>ADULTS (18+)</Form.Label>
                  <div className='d-flex formm'>
                  <select className="form-control" name='Localclass'>
                  <option>1</option>
                          <option>2</option>
                          <option>3</option>
                  </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>
                  </div>
                </Form.Group>
              </div>
              <div className='col-md-2 cont' >
              <Form.Group controlId="formDepartureDate" >
                  <Form.Label>CHILDREN (0-17)</Form.Label>
                  <div className='d-flex formm'>
                  <select className="form-control" name='LocalChildrenClass'>
                          <option>Children (0-17)</option>
                          <option>1</option>
                          <option>2</option>
                          <option>3</option>
                      </select> <i className="fa fa-long-arrow-down" aria-hidden="true"></i>
                      </div>
                </Form.Group>
              </div>
            </Row>
            <Row className="mb-3 justify-containt-center">
            <div className='col-md-4 cont'  >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>Name</Form.Label>
                  <Form.Control type="text" placeholder=' Name'  name='Localuser'/>
                </Form.Group>
              </div>
              <div className='col-md-4 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>PHONE</Form.Label>
                  <Form.Control type="text" placeholder=' Phone Number' name="Localphone" />
                </Form.Group>
              </div>
              <div className='col-md-4 cont' >
                <Form.Group controlId="formDepartureDate">
                  <Form.Label>E-MAIL</Form.Label>
                  <Form.Control type="text" placeholder=' E-mail' name='Localemail' />
                </Form.Group>
              </div>
              <div className='col-md-5 ' >
            <div className='btnalign'>
              <Button variant="primary" type="submit">
            Submit
            </Button>
            </div>
              </div>
            </Row>
           
          </Form>
        </Tab.Pane>
        
      </Tab.Content>
    </Tab.Container>
    </div>
  );
};

export default Tab1;
