import React from 'react'
import "./Car-Rental.css"
import TabC from "./TabC"
import Login from '../Login/Login'
import CarService from './CarService'
import Slider from 'react-slick';
import LogosMin from '../FleetsS1/LogosMin';

const CarRental = () => {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 2000,
    responsive: [
    {
    breakpoint: 1024,
    settings: {
    slidesToShow: 4,
    slidesToScroll: 1,
    infinite: true,
    dots: true
    }
    },
    {
    breakpoint: 600,
    settings: {
    slidesToShow: 2,
    slidesToScroll: 1,
    initialSlide: 2
    }
    },
    {
    breakpoint: 480,
    settings: {
    slidesToShow: 2,
    slidesToScroll: 1,
    initialSlide: 2
    }
    }
    ]
    };
return (
<>
<div className="hero-block hero-v1 herrobg1 bg-img-hero-bottom gradient-overlay-half-black-gradient text-center z-index-2"  >
   <div className="container space-3 space-top-xl-5">
      <div className="row pb-xl-8">
         <div className="col-md-12">
            <h1 className="font-size-40 font-size-xs-30  font-weight-bold text-uppercase text-left" style={{color: "#00aeef"}} >LET'S THE WORLD !<br/>
            <span style={{color: "#f58220"}}> TOGETHER</span>
            </h1>
         </div>
         <div className="col-md-11">
            <TabC/>
         </div>
      </div>
   </div>
</div>
<section className="pt-4 pb-4 ">
   <div className="container">
      <div className="row">
         <div className="col-md-8">
            <div className="containtC">
               <h2>Experience the freedom of the open road with SafewayGo's Car Rental Services.
               </h2>
               <p>Experience the freedom of the open road with SafewayGo's Car Rental Services. Whether you're traveling for business or leisure, we've got the perfect wheels to get you there in style. Explore the world on your terms with our reliable and convenient car rental options.
               </p>
               <div className="row pt-3">
                  <div className="col-md-6 col-lg-6 col-xl-6 mb-3 mb-md-6 pb-1">
                     <div className="support-content">
                        <h3>Wide Fleet Selection
                        </h3>
                        <p>From compact cars to spacious SUVs and luxury vehicles, our extensive fleet ensures you find the ideal car for your journey.</p>
                     </div>
                  </div>
                  <div className="col-md-6 col-lg-6 col-xl-6 mb-3 mb-md-6 pb-1">
                     <div className="support-content">
                        <h3>Convenience
                        </h3>
                        <p>Our hassle-free booking process and convenient pickup and drop-off locations make renting a car with us a breeze.</p>
                     </div>
                  </div>
                  <div className="col-md-6 col-lg-6 col-xl-6 mb-3 mb-md-6 pb-1">
                     <div className="support-content">
                        <h3>Competitive Pricing
                        </h3>
                        <p>We offer competitive rates and transparent pricing, ensuring you get the most value for your money.</p>
                     </div>
                  </div>
                  <div className="col-md-6 col-lg-6 col-xl-6 mb-3 mb-md-6 pb-1">
                     <div className="support-content">
                        <h3>Safety First
                        </h3>
                        <p>Your safety is our priority. Our vehicles are well-maintained and regularly serviced to ensure a worry-free journey.</p>
                     </div>
                  </div>
               </div>
            </div>
         </div>
         <div className="col-md-4">
            <Login/>
         </div>
      </div>
   </div>
</section>
<section className="pt-5 pb-5 bg-img1 dark">
   <div className="container">
      <div className="row">
          <div className="col-md-12 text-center">
            <div className='box-1'>
              <h2 className='text-white'>Start Your Road Trip
              </h2>
              <p className='text-white'>Ready to hit the road? Explore our car rental options, and let your journey begin.<br/>
              SafewayGo is your trusted partner for a memorable travel experience.</p>
              <a class="btn btn-outline-white border-width-2 rounded-xs min-width-200 font-weight-normal transition-3d-hover text-white" href="contact-us.html">Contact Us</a>
          </div>
          </div>
      </div>
      </div>
      </section>
      <section className="pt-5 pb-5 bg-light">
    <div className="container">
      
      <div className="row justify-content-center">
                <div className="col-md-12">
                <div className="w-md-80 w-lg-50 text-center mx-md-auto mb-5 mt-4">
                      <h2 className="section-title text-black font-size-30 font-weight-bold mb-0">
                      Our Services


                  </h2>
              </div>

</div>
{
  CarService.map((elm)=>{
    return(
      <>
      <div className="col-lg-3 col-sm-3 col-md-6">
                    <div className="saas_features_item text-center">
                        
                        
                        <div className="new_service_content">
                            <img src={elm.img1} alt="Alternate Text"/>
                            <h3 className="sub-head">{elm.title} </h3>
                            <p>
                                {elm.dis}
                            </p>

                        </div>
                    </div>
                </div>
      </>
    )

  })
}

</div>
</div>
</section>

<section className="pt-5 pb-5 " style={{overflowX:'hidden'}}>
   <div className="container">
      <div className="row">
         <div className="col-md-12">
            <div className="w-md-80 w-lg-50 text-center mx-md-auto mb-5 mt-4">
               <h2 className="section-title text-black font-size-30 font-weight-bold mb-0">
                  Our Fleets
               </h2>
            </div>
         </div>
         <div className="col-md-12">
            <div className="client-logo-carousel">
               <Slider {...settings}>
                  {LogosMin.map((emlm, index) => (
                  <div key={index}>
                     <img src={emlm.imgrec} alt={`Client logo ${index + 1}`} />
                  </div>
                  ))}
               </Slider>
            </div>
         </div>
      </div>
   </div>
</section>
</>
)
}
export default CarRental