import React, { useState } from 'react';
import "./Whatsapp.css"
const Whatapp = () => {
    const sendMessage = () => {
        const phoneNumber = '919880424375'; // Replace with the recipient's phone number
        const message = encodeURIComponent('Hello, How are You Today !');
        const url = `https://api.whatsapp.com/send?phone=${phoneNumber}&text=${message}`;
        window.open(url, '_blank');
      };
    
  return (
    <>
   <button className="whatsapp-button" onClick={sendMessage}>
      <i class="fa fa-whatsapp" aria-hidden="true"></i> Send Message 
    </button>
    </>
  )
}

export default Whatapp