import React from 'react'
import GalleryData from './GalleryData'
import "./Gallery.css"
const Gallery = () => {
  return (
    <>
       <div className="hero-block hero-v1 bg-img-hero-bottom gradient-overlay-half-black-gradient text-center z-index-2" >
   <div className="container space-3 space-top-xl-5">
      <div className="row pb-xl-8">
         <div className="col-md-12">
            <h1 className="font-size-40 font-size-xs-30  font-weight-bold text-uppercase text-left" style={{color: "#f58220"}} >Gallery </h1>
         </div>
         <div className="col-md-12">
            <nav className="py-3" aria-label="breadcrumb">
               <ol className="breadcrumb breadcrumb-no-gutter mb-0 flex-nowrap flex-xl-wrap overflow-auto overflow-xl-visble">
                  <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="index.html">Home</a></li>
                  <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="#">Gallery</a></li>
               </ol>
            </nav>
         </div>
      </div>
   </div>
</div>
<section className="pt-5 pb-5">
   <div className="container">
      <div className="row">
         {
            GalleryData.map((elm)=>{
                  return(
                     <>
                        <div className="col-md-4 col-lg-4 col-sm-3 col-6">
                           <div className="gallery">
                                 <img src={elm.glsrc} alt="" />
                           </div>
                     </div>
                     </>
                  )
            })
         }
         
      </div>
   </div>
</section>
    </>
  )
}

export default Gallery