import React from 'react'
import "./Banner.css"
import banner from "../img/banner.png";

import Tab1 from './Tab1';
const Banner = () => {
  
  return (
   <>
   <section className="home">
    <div className="overlay"></div>
    
    <img src={banner} alt="" />
   <div className="homecontant container">
    <div className="row align-items-center">
      <div className="col-md-7 col-lg-7 col-sm-7">
      <div className="textDiv">
      <span className="smalltext" data-aos="fade-up">
      WELCOME TO SAFEWAY GO
      </span>
      <h1 className="hedingmai" data-aos="fade-up">
      DISCOVER YOUR DREAM<br /> DESTINATIONS
      </h1>
    </div>
      </div>
      <div className="col-md-5 col-lg-5 col-sm-5">
    
        </div>
    </div>
   
  
   
   </div>
   </section>
   <section>
    <div className="container">
      <div className="row">
        <div className="col-md-12">
        <Tab1/>
        </div>
      </div>
    </div>
   </section>
   </>
  )
}

export default Banner