import React from 'react'
import "./Testimonial.css"
import { TestiData } from './TestiData'
const Testimonial = () => {
  return (
    <section className="pt-5 pb-5 ">
      <div className="container">
        
        <div className="row">
                  <div className="col-md-12">
                  <div className="w-md-80 w-lg-50 text-center mx-md-auto mb-5 mt-4">
                        <h2 className="section-title text-black font-size-30 font-weight-bold mb-0">
                    Explore the Customers' experience

                    </h2>
                </div>

</div>
{
  TestiData.map((elm)=>{
    return(
    <div className="col-md-4">
    <div className='textibox'>
    <div className="ratings d-flex justify-content-center">
    
              <i className="fa fa-star"></i>
              <i className="fa fa-star"></i>
              <i className="fa fa-star"></i>
              <i className="fa fa-star"></i>
          </div>
          <div className="tour-text color-grey-3 text-center">{elm.tstD}</div>
          <h5 className="link-name d-flex justify-content-center testminoal-top-margin">{elm.testName}</h5>
          <h6 className="link-position d-flex justify-content-center" style={{marginTop:"10px;"}}>{elm.testPlace}</h6>
    </div>
    </div>
    )
  })
}


</div>
</div>
</section>
  )
}

export default Testimonial