import React from 'react'
import {FitureData} from './FitureData'
import "./Ftures.css"
const Fitures = () => {
  return (
   <>
      <section className='pb-4 pt-4'>
          <div className="container">
              <div className="row">
                {
                  FitureData.map(( elm)=>{
                    return(
                    <div className="col-md-3 col-lg-3 col-6">
                      <div className="imgC">
                        <div className="imgs">
                          <img src={elm.imgsc} alt="" />
                        </div>
                        <div className="content d-flex justify-content-center align-items-center">
                          <h4>{elm.conten}</h4>
                        </div>
                    </div>
                    </div>
                    )
                  })
                }

                
                
              </div>
          </div>
      </section>
   
   </>
  )
}

export default Fitures