import React from 'react'
import "./Destination.css"

import InnsedestinatioData from '../InnerPagesDestination/InnsedestinatioData';

import {Link } from 'react-router-dom';
const Destination = () => {
  return (
   <section className="pt-5 pb-5 bg-light">
      <div className="container">
        
        <div className="row">
<div className="col-md-12">
<div class="w-md-80 w-lg-50 text-center mx-md-auto mb-5 mt-4">
                    <h2 class="section-title text-black font-size-30 font-weight-bold mb-0">
                        Discover Your Destinations
                    </h2>
                </div>
</div>
          {
            InnsedestinatioData.map((elm, id)=>{
              return(
                <div className="col-md-3">
              <div key={id} className="min-height-350 mb-4 bg-img-hero rounded-border p-5 gradient-overlay-half-bg-gradient transition-3d-hover shadow-hover-2 border-0 dropdown" style={{backgroundImage: `url(${elm.imdrs})`,backgroundRepeat:'no-repeat',backgroundSize: "cover"}}>
              <div className="w-100 d-flex justify-content-between mb-2">
                  <div className="position-relative">
                  <Link to={`/InnsedestinatioData/${elm.id}`}  className="destination text-white font-weight-bold font-size-21 pb-3 mb-1 text-lh-1 d-block">{elm.titele}</Link>
                      <Link to={`/InnsedestinatioData/${elm.id}`} className='home-read-more'>Read More</Link>
                      </div>
                  </div>
              </div>
          </div>)
            })
          }
        </div>
      </div>
   </section>
  )
}

export default Destination