import React from 'react'
import "./Fleet.css"
import icon from "../img/icon-team-builder.svg"
import icon2 from "../img/icon-calculator.svg"
import icon3 from "../img/icon-karma.svg"
import icon4 from "../img/icon-supervisor.svg"
import imgw from "../img/bus_right.png"
const Fleet = () => {
  return (
    <section className="pt-5 pb-5 " style={{background: "linear-gradient(90deg, rgb(239 248 251) 0%, rgb(229 183 143) 100%, rgb(252, 176, 69) 100%)"}}>
    <div className="container">
      
      <div className="row align-items-center">
        <div className="col-md-6 col-sm-6">
          <div class="row">
                        <div class="col-lg-6 col-sm-6">
                            <div class="card special-section bg-primary-light mmt-5">

                                <div class="card-body first-cbody">
                                   <div className="icon-w">
                                    <img src={icon} alt="" />
                                   </div>
                                    <h5 class="big-head">Fleet1r</h5>
                                    <p>
                                    Scans our talent network to create the optimal team for your project
                                    </p>
                                </div>
                            </div>
                            <div class="card special-section bg-success-light">

                                <div class="card-body second-cbody">
                                <div className="icon-w">
                                    <img src={icon2} alt="" />
                                   </div>
                                    <h5 class="big-head">Fleet1</h5>
                                    <p>
                                    Uses data from past projects to provide better delivery estimates
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div class="col-lg-6 col-sm-6 mt-10">
                            <div class="card special-section bg-secondary-light">

                                <div class="card-body third-cbody">
                                <div className="icon-w">
                                    <img src={icon3} alt="" />
                                   </div>
                                    <h5 class="big-head">Fleet1</h5>
                                    <p>
                                    Regularly evaluates our talent to ensure quality
                                    </p>
                                </div>
                            </div>
                            <div class="card special-section bg-info-light">
                                <div class="card-body fourth-cbody">
                                <div className="icon-w">
                                    <img src={icon4} alt="" />
                                   </div>
                                    <h5 class="big-head">Fleet1</h5>
                                    <p>
                                    Monitors activity to identify project roadblocks
                                    </p>
                                </div>
                            </div>
                        </div>
                    </div>
          </div>
          <div className="col-md-1 col-sm-1">
            
          </div>
          <div className="col-md-5 col-sm-6">
            <img src={imgw} alt="" className='img-fluid' />
          </div>
  </div>
  </div>
  </section>
  )
}

export default Fleet