import React from 'react'
import { Form, Button} from 'react-bootstrap';
import "./Login.css"
const Login = () => {


  return (
    <>
    <div className="border border-color-7 form-box   position-relative" style={{background:"#eaf3f6 "}}>
                            <h6 className="font-size-30 font-weight-bold  mx-1 mb-3 pb-1 ">
                                Request A Callback
                            </h6>
                            <Form action='https://formspree.io/f/xoqgoyqz' method='post'>
                            <div className="row form--wrap ">
                                <div className="col-lg-12">
                                    <div className="fontuser">
                                        <label className="label-left">Name</label>
                                        <input type="text" placeholder="Enter Username" name="Car_UserName" required="" />
                                        <i className="fa fa-user" aria-hidden="true"></i>
                                    </div>
                                    <div className="fontuser">
                                        <label className="label-left">Phone</label>
                                        <input type="text" placeholder="Enter Phone" name="Car_UserPhone" required="" />
                                        <i className="fa fa-phone" aria-hidden="true"></i>
                                    </div>
                                </div>
                                <div className="col-lg-12">
                                    <div className="fontuser">
                                        <label className="label-left">E-mail</label>
                                        <input type="text" placeholder="Enter E-mail" name="Car_UserEmail" required="" />
                                        <i className="fa fa-envelope" aria-hidden="true"></i>
                                    </div>
                                </div>
                                <div className="col-lg-12">
                                    <div className="fontuser">
                                        <label className="label-left">Message</label>
                                        
                                        <textarea id="w3review" name="Car_UserMessage" rows="4" cols="50"></textarea>
                                        <i className="fa fa-map-marker" aria-hidden="true"></i>
                                    </div>
                                    <div className="fontuser half-form form-right-margin mt-3 d-flex align-items-center">
                                        <Button type="submit" className="btn btn-primary">Submit</Button>
                                    </div>
                                </div>
                            </div>
                            </Form>
                            
                        </div>
    
    </>
  )
}

export default Login