import React from 'react'
import "./Blog.css"
import Slider from 'react-slick';
import BlogData from './BlogData';
import {Link } from 'react-router-dom';
const Blog1 = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    autoplay: true,
    autoplaySpeed: 3000,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
          infinite: true,
          dots: true
        }
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1
        }
      }
    ]
  };
  
  return (
    <section className="pt-5 pb-5 bg-light">
    <div className="container">
      
      <div className="row">
                <div className="col-md-12">
                <div className="w-md-80 w-lg-50 text-center mx-md-auto mb-5 mt-4">
                      <h2 className="section-title text-black font-size-30 font-weight-bold mb-0">
                  Explore the Customers' experience

                  </h2>
              </div>

</div>
<div className="col-md-12">
<div className="blog-carousel">
      <Slider {...settings}>
        {BlogData.map((post, index) => (
          <div key={index} className="blog-post">
          <Link to={`/BlogData/${post.index}`} className='text-dark'>  <img src={post.image} alt={post.title} /></Link>
          <Link to={`/BlogData/${post.index}`} className='text-dark'> <h3>{post.title}</h3></Link>
          <Link to={`/BlogData/${post.index}`} className='text-dark'> <p>{post.excerpt}</p></Link>
          </div>
        ))}
      </Slider>
    </div>
</div>
</div>
</div>
</section>
  )
}

export default Blog1