import React, {useState} from 'react';
import Slider from 'react-slick';
import SliderData from './SliderData';
import { useParams } from 'react-router-dom';
import InnsedestinatioData from '../Destination1/DestinationData';
import 'react-image-gallery/styles/css/image-gallery.css';
import ImageGallery from 'react-image-gallery';
import "./HeritageTourkarnataka.css"
import foot from "../img/phone-call.png"
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';
import Login from "../Login/Login"
import { Link, animateScroll as scroll } from 'react-scroll';

const HeritageTourkarnataka = () => {
   const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
const { id } = useParams();
const InnsedestinatioDatan = InnsedestinatioData.find(InnsedestinatioDatan => InnsedestinatioDatan.id === parseInt(id));
if (!InnsedestinatioDatan) {
return 
<h2>Blog not found</h2>
;
}

var settings = {
speed: 500,
slidesToShow: 1,
slidesToScroll: 1,
infinite: true,
autoplay: true,
autoplaySpeed: 3000,
};

return (
<>

<section  style={{overflowX:'hidden', background: "rgb(234, 243, 246)"}} className='position-relative  '>
   <div className="container-fluid g-0">
      <div className="row">
         <div className="col-md-12">
            <div className="Banner ">
               <Slider {...settings}>
                  {SliderData.map((emlm, index) => (
                  <div key={index}>
                     <img src={emlm.imrec}  />
                     <div className="container ">
                        <div className="row">
                           <div className="col-md-12 con">
                              <h1 className="font-size-40 font-size-xs-30  font-weight-bold text-uppercase text-left" style={{color: "#f58220"}} >DESTINATIONS
                              </h1>
                              <div className="">
                                 <nav className="py-3" aria-label="breadcrumb">
                                    <ol className="breadcrumb breadcrumb-no-gutter mb-0 flex-nowrap flex-xl-wrap overflow-auto overflow-xl-visble">
                                       <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="index.html" className='text-white'>Home</a></li>
                                       <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="#" className='text-white'>DESTINATIONS
                                          </a>
                                       </li>
                                       <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="#" className='text-white'>{InnsedestinatioDatan.titele}
                                          </a>
                                       </li>
                                    </ol>
                                 </nav>
                              </div>
                           </div>
                        </div>
                     </div>
                  </div>
                  ))}
               </Slider>
            </div>
         </div>
      </div>
   </div>
</section>
<section className="pt-5 pb-5 " style={{background: "rgb(234, 243, 246)"}}>
   <div className="container">
      <div className="row justify-containt-center maintop">
         <div className="col-md-7">
         <div className="position-sticky">
                  <ul className='d-flex gap-3 border-bottom  z-index-2'>
                     <li className='p-2'>  <Link
          activeClass="active"
          to="section1"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >Itinerary </Link></li>
                     <li className='p-2'><Link
          activeClass="active"
          to="gallery"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >Gallery</Link></li>
                      <li className='p-2'><Link
          activeClass="active"
          to="map"
          spy={true}
          smooth={true}
          offset={-70}
          duration={500}
        >Map</Link></li>
                     
                  </ul>
               </div>
            <div  className="min-height-350 mb-4 bg-img-hero rounded-border pt-4 " >
            {
               InnsedestinatioDatan.innrData.map((elm)=>{
                     return(
                        <>
                        <div className="w-100 d-flex justify-content-between mb-4  " id='section1'  style={{backgroundColor: "#fff"}}>
                        <div className="position-relative boderl">
                           {/* <a href="#!" className="destination text-white font-weight-bold font-size-21 pb-3 mb-1 text-lh-1 d-block">{InnsedestinatioDatan.titele}</a> */}
                           <div class="row_day">
                              <div class="day">
                                 <div class="daydesc">
                                    {elm.day}
                                    <br/>
                                 </div>
                              </div>
                              <div class="desc">
                                {elm.dayTile1}
                              </div>
                           </div>
                           <div className='p-4'>
                              <p>{elm.day1}</p>
                             <p>{elm.dis}</p>
                              <p><strong> {elm.hed1}</strong> {elm.dis1}</p>
                              <p><strong> {elm.hed2}</strong> {elm.dis2}</p>
                              <p><strong> {elm.hed3}</strong> {elm.dis3}</p>
                              <p><strong> {elm.hed4}</strong> {elm.dis4}</p>
                           </div>
                          
                        </div>
                        </div>
                        </>
                     )
               })
              }
              <div id="gallery">
           <ImageGallery
           
           showThumbnails={true}
           showFullscreenButton={true}
           showPlayButton={true}
           autoPlay={true}
           slideInterval={3000}
           fullscreen={false}
           
           items={InnsedestinatioDatan.images1} />
         </div>
         </div>
         <div id='map'>
                           <iframe  key={InnsedestinatioDatan.id}     src={InnsedestinatioDatan.src} width="100%" height="450" style={{border:"none"}}  allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
                        </div>
      </div>
      <div className="col-md-3 postion-sticky-work">
      <div >
         <div className="mybox mb-4">
               <h4>Ask Your Question</h4>
               <hr />
               <p>Thank you for reaching out with your query about our tour.</p>
               <div className='d-flex icon-work'>  <img src={foot} alt="" /> <a href="tel:+91-98804 24375">+91-98804 24375</a></div>
         </div>
         <div className="mybox mb-4">
               <h4>Share <i className='fa fa-share'></i> </h4>
               <hr />
               <div className="contact-cws-social">
                                    <a className="cws-social fa fa-facebook text-white" href="#" target="_blank"></a>
                                    <a className="cws-social fa fa-instagram text-white" href="#" target="_blank"></a>
                                    <a className="cws-social fa fa-youtube text-white" href="#" target="_blank"></a>
                                </div>
         </div>
         
         <div class="call-box ">
    <h2>Quick Enquiry  </h2>
    <p>We will call you back as soon as possible</p>
    <Button className="call-to-action" onClick={handleShow}>Get a Callback</Button>
  </div>
      </div>
      </div>
   </div>
   </div>
</section>
<Modal
        show={show}
        onHide={handleClose}
        backdrop="static"
        keyboard={false}
      >
        <Modal.Header closeButton>
        
        </Modal.Header>
        <Modal.Body>
         <Login/>
        </Modal.Body>
        
      </Modal>
</>
)
}
export default HeritageTourkarnataka