import React from 'react'
import BlogData from '../Blogs1/BlogData';
import "./BlogInner.css"

import {Link } from 'react-router-dom';
const Blog = () => {
  return (
    <>
    <div className="hero-block hero-v1 bg-img-hero-bottom gradient-overlay-half-black-gradient text-center z-index-2" >
   <div className="container space-3 space-top-xl-5">
      <div className="row pb-xl-8">
         <div className="col-md-12">
            <h1 className="font-size-40 font-size-xs-30  font-weight-bold text-uppercase text-left" style={{color: "#f58220"}} >Blog </h1>
         </div>
         <div className="col-md-12">
            <nav className="py-3" aria-label="breadcrumb">
               <ol className="breadcrumb breadcrumb-no-gutter mb-0 flex-nowrap flex-xl-wrap overflow-auto overflow-xl-visble">
                  <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="index.html">Home</a></li>
                  <li className="breadcrumb-item flex-shrink-0 flex-xl-shrink-1"><a href="#">Blog</a></li>
               </ol>
            </nav>
         </div>
      </div>
   </div>
</div>
<section className="pt-5 pb-5 ">
    <div className="container">
      
      <div className="row">
        {
            BlogData.map((post)=>{
              return(
              <div className='col-md-3 col-lg-3 col-sm-4 col-12 type_block_product new-pdetails '>
              <div className='product-container blog-box'>
              <div class="blog-imgsec">
              <Link to={`/BlogData/${post.index}`} className='text-dark'>  <img src={post.image} class="img-fluid" alt={post.title} /></Link>
                
                
                </div>
                <div className="containtB">
               
          <Link to={`/BlogData/${post.index}`} className='text-dark'> <h5>{post.title}</h5></Link>
          <Link to={`/BlogData/${post.index}`} className='text-dark'> <p>{post.excerpt}</p></Link>
                   
          <Link to={`/BlogData/${post.index}`} className="btn btn-primary text-white">Read More</Link>
                </div>
              </div>
    
            </div>
            )
            })
        }
        
        </div>
        </div>
        </section>
    </>
  )
}

export default Blog